<template>
     <div>
         <button v-on:click="get">get</button>
         <button v-on:click="put">put</button>
         <button v-on:click="post">post</button>
         <button v-on:click="deleteB">delete</button>
     </div>
</template>

<script>



export default {
    name: 'HelloWorld',
    data() {
        return {
           
        }
    },
    methods: {
        get: function(){
            fetch(authorUrl,{
                credentials: 'include'
            },{  
                method: 'get',
                mode: 'cors'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
                console.log(data);
        });
        },
        put: function(){
                        fetch(authorUrl, {
                method: 'put',
                mode: 'cors',
                headers: {  
                    "Content-type": "application/x-www-form-urlencoded; charset=UTF-8",

                },
                credentials: 'include', 
                body: 'userId='+JSON.stringify(12)+'&bookId='+JSON.stringify(12)
                +'&count='+JSON.stringify(12)+'&hash='+JSON.stringify(12)
            })
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function (data) {
                console.log(data)
                // if(data == false){
                //     alert('Some error');
                // }
            });
        },
        post: function(){
                        fetch(authorUrl, {
                method: 'post',
