            })
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function (data) {
                console.log(data);
                // if(data != false){
                //     alert('Book successful added to bag');
                // }
            });
        },
        deleteB: function(){
            fetch(authorUrl, {
                    method: 'delete',
                    headers: {  
                        "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                    },
                    mode: 'cors',
                    credentials: 'include',
                    body: 'userId='+JSON.stringify(213)+'&deleteArr='+JSON.stringify(123)
                    +'&hash='+JSON.stringify(321)
                })
                .then(this.$parent.status)
                .then(this.$parent.json)
                .then(function (data) {
console.log(data);
console.log(data.headers);
                    // if(data == true){
                    //     for(var key in self.deleteArr){
                    //         self.$delete(self.bag, self.deleteArr[key]);
                    //     }
                    //     self.deleteArr = [];
                    //     alert('Book successful deleted from Bag');
                    // }
                });
        }
        
    }
}
</script>

