<template>
<div>
    <div >
      <button type="button" class="btn btn-success home"
          v-on:click="home" >Home</button>
    </div>
  <div class="container ">
    <div class="col-md-3"></div>
    <div class="col-md-6 usersContent">
        <div v-for="user in users"
        class="userAct">
            <div class="user">
                <p>
                    {{user.name}}
                </p>
            </div>
            <span v-if="user.isActive=='active'"class="active">{{user.isActive}}</span>                
            <span v-else class="noActive">{{user.isActive}}</span>                

                <button class="btn btn-info" v-on:click="editUser(user.id)">Edit</button>
                <button v-if="user.isActive == 'active'"class="btn btn-danger">Remove</button>
      
        </div>

    </div>
    <div class="col-md-3"></div>
  </div>
    <div class="addUser">
        <button class="btn btn-primary"
        v-on:click="addNewUser">Add New User</button>
    </div>
</div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
        users: ''
    }
  },
  methods: {
    addNewUser: function () {
        this.$router.push({ path: '/addNewUser'});
    },
    editUser: function (userId) {
        this.$router.push({ path: '/editUser/'+userId});
    },
    home: function () {
        this.$router.push({ path: '/mainPage/calendar/1'});
    }
  },
  created () {
      var self = this;
            fetch(userUrl,{  
            method: 'get',
            // headers: {  
            //     // "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            //     // "Cookie": "hash=" + hash + 'id='+ id
            // },
            // mode: 'cors',
            credentials: 'include'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
            if(data != false){
                self.users = data;
            }
            console.log(data);
        //   self.options.authors = data;       
      });

  }

}
</script>

<style>

.usersContent{
    border: 2px solid #778899; 
    background-color: #FAFAD2;
    border-radius: 3px;
}

.user{
    background-color: #90EE90;
    width: 150px;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 40px;
    border: 1px solid #778899; 
    border-radius: 3px;
    text-align: center;
    display: inline-block;
}
.user > p {
    margin-top: 10px;
    display: inline-block;
}

.userAct > span {
    margin-left: 50px; 
}
.userAct > button{
    float: right;
    margin-top: 15px;
    margin-right: 30px;
}
/* .user > button{
    display: inline-block;
} */
#app{
    /* background-color: 	#3CB371; */
}

.noActive{
    color: red;
}

.active {
    color: green;
}

.addUser{
    text-align: center;
    margin-top: 20px;
}
</style>



