import Vue from 'vue'
import Router from 'vue-router'

import calendar from '@/components/calendar/calendar'
import restTest from '@/components/calendar/restTest'
import editEvent from '@/components/calendar/editEvent'
import loginPage from '@/components/calendar/loginPage'
import mainPage from '@/components/calendar/mainPage'
import bookIt from '@/components/calendar/bookIt'
import employee from '@/components/calendar/employee'
import addNewUser from '@/components/calendar/addNewUser'
import editUser from '@/components/calendar/editUser'

// import changeAuthor from '@/components/changeAdminComponents/changeAuthor'

// import VeeValidate from 'vee-validate';

// Vue.use(VeeValidate);
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'loginPage',
      component: loginPage
    },
    {
      path: '/bookIt/:roomId',
      name: 'bookIt',
      component: bookIt
    },
    {
      path: '/mainpage',
      name: 'mainPage',
      component: mainPage,
      children: [
        {
          path: 'calendar/:id',
          components:{calendar: calendar}
          // canReuse: false
        }
      ]
    },
    {
      path: '/calendar/:id',
      name: 'calendar',
      component: calendar,
      // canReuse: false
    },
    {
      path: '/employee',
      name: 'employee',
      component: employee,
      // canReuse: false
    },
    {
      path: '/addNewUser',
      name: 'addNewUser',
      component: addNewUser,
      // canReuse: false
    },
    {
      path: '/editUser/:userId',
      name: 'editUser',
      component: editUser,
      // canReuse: false
    },
    {
      path: '/editEvent/:bedroomId/:userId/:eventId/:startTime/:eventDate',
      name: 'editEvent',
      component: editEvent
    }
  ]
})



