<template>
     <div class="row">
         <div class="roomsBoard">
             <div class="roomBoard" v-for="room in rooms">
                 <button class="btn btn-info" 
                 v-on:click="showBoardroom(room.id,$event)">
                     {{room.name}}
                 </button>
             </div>
             <!-- <div class="roomBoard">
                 <button class="btn btn-info" v-in:click="showBoardroom(2)">Boardroom 2</button>
             </div>
             <div class="roomBoard">
                 <button class="btn btn-info" v-in:click="showBoardroom(3)">Boardroom 3</button>
             </div> -->
         </div>
  <router-view class="calendar"
   v-if="room"
   name="calendar"></router-view>
   <div>
       <div class="actionBtn">
           <div>
                <button class="btn"
                v-on:click="asd">Book it!</button>
           </div>
           <div>
                <button class="btn"
                v-on:click="employeeList">Employee List</button>
           </div>
       </div>
   </div>
  </div>
</template>

<script>

export default {
    data() {
        return {
            rooms: [{'id':1,'name':'Boardroom 1'},
            {'id':2,'name':'Boardroom 2'},
            {'id':3,'name':'Boardroom 3'}],
            room: true,
            roomId: ''
        }
    },
    methods: {
        asd: function () {
            // this.room = true;
            this.$router.push({ path: '/bookit/'+this.roomId});
            // console.log(this.$router)
        },
        employeeList: function () {
            this.$router.push({ path: '/employee'});
        },
        showBoardroom: function (roomId,event) {
            // event.preventDefault();
            // console.log(event);
            // this.room = false;
            // this.asd(roomId)
            this.$router.push({ path: '/mainpage/calendar/'+roomId});
            // this.room = true;
            // event.p
        }
    },
    created() {
        this.roomId = this.$route.params.id;
        // console.log(this.$route.params.id)
    },
    watch: {
    '$route':function (to, from) {
        this.roomId = to.params.id;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.roomBoard{
    display: inline-block;
    text-align: center;
    margin-left: 20px;
}
.roomsBoard{
    text-align: center;
}
.roomBoard >button:target  {
  color: #BA55D3
}
.calendar{
    float: left;
}
.actionBtn{
    margin-top: 50px;
    text-align: center;
}
.actionBtn > div{
    margin-top: 20px;
    /* text-align: center; */
}
</style>



